body {
    font: 14px "Century Gothic", Futura, sans-serif;
    display: flex;
    justify-content: center;
    line-height: 20vh;
    align-items: center;
    background-color: #14bdac;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    color: blue;
    margin-bottom: 20px;
    font-size: 25px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 40px;
    line-height: 34px;
    height: 120px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 120px;
    color: red;
  }
  
  .square:focus {
    outline: none;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  